body {
  color: #1f1f1f;
  background: linear-gradient(#bada55, #c0ffee);
  height: 100vh;
}

.test {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: flex;
}

h1 {
  color: #ff69b4;
  font-family: cursive;
}

header {
  border-bottom: 1px solid #eee;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1.5rem;
  display: flex;
}

nav a {
  margin-left: 1rem;
  text-decoration: none;
}

.hero {
  background: #f6f8ff;
  padding: 3rem 1.5rem;
}

.grid {
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1rem;
  padding: 1.5rem;
  display: grid;
}

.card {
  display: flex;
}

.card .title {
  color: #1f1f1f;
}

button {
  cursor: pointer;
  color: var(--main-color);
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: .6rem 1rem;
}
